<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'catalog' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title" v-if="orderInfo">
          <span>{{ this.$route.params.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;   
            &nbsp; .    &nbsp; &nbsp;  
              .
          </p>

          <ul class="dictionary" v-if="orderInfo">
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ orderInfo.status.title }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ orderInfo.paymentType }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.comment }}
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders" v-if="orderInfo">
            <OrderItem v-for="item in orderInfo.basket.items" :key="item.id" :item="item" />
          </ul>

          <div class="cart__total" v-if="orderInfo">
            <p>
              : <b>{{ this.orderInfo.deliveryType.price }} </b>
            </p>
            <p>
               : <b>{{ this.orderInfo.basket.items.length }}</b>  
              <b
                >{{
                  (Number(this.orderInfo.totalPrice) + Number(this.orderInfo.deliveryType.price))
                    | numberFormat
                }}
                </b
              >
            </p>
          </div>
        </div>
        <vue-modaltor :visible="open" @hide="closeModal" :show-close-button="false">
          <div class="cart__error form__error-block" style="text-align:center;">
              <h4>!</h4>
              <p>
                {{ orderErrorMessage }}
              </p>
          </div>
        </vue-modaltor>
      </form>
    </section>
  </main>
</template>

<script>
import OrderItem from '@/components/Order/OrderItem.vue';
import numberFormat from '@/helpers/numberFormat';
import { mapState, mapActions } from 'vuex';

export default {
  components: { OrderItem },
  filters: { numberFormat },
  computed: {
    ...mapState('order', [
      'orderInfo',
      'open',
      'orderError',
      'orderErrorMessage',
    ]),
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadOrder();
      },
      immediate: true,
    },
  },
  methods: {
    ...mapActions('order', [
      'loadOrderInfo',
      'closeModal',
    ]),

    loadOrder() {
      this.loadOrderInfo({
        userAccessKey: this.$store.state.userAccessKey,
        id: this.$route.params.id,
      }).catch(() => {
        this.$router.replace('/notFound');
      });
    },
  },
};
</script>
